export const DataRoomSection = styled.div`
  background-color: #fff;
  padding: 1rem 1rem;
  color: #000;
`;

// Title
export const Title = styled.h2`
  color: #335795;
  font-weight: 700;
  font-size: 2rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-bottom: 1rem;
`;

// Table header cells
export const TableHeader = styled.th`
  background-color: #000 !important;
  color: #ffff !important;
  text-align: center;
  vertical-align: middle;
  font-size: 13px;
  text-transform: capitalize;

  &:first-child {
    text-align: start;
  }
`;

// Table data cells
export const TableData = styled.td`
  vertical-align: middle;
  text-align: center;

  h6 {
    font-size: 13px;
    font-weight: 600;
    text-transform: capitalize;
    text-align: start;
    color: #000;
  }

  h5 {
    font-size: 13px;
    font-weight: 400;
    color: #335795;
    text-transform: capitalize;
  }

  p {
    font-size: 13px;
    color: #000;
    margin: 0;
    text-transform: capitalize;
  }
`;

// Upload Button
export const UploadButton = styled.button`
  background-color: #335795;
  color: #fff;
  border: none;
  padding: 4px 10px;
  font-weight: 400;
  font-size: 12px;
  border-radius: 4px;
  text-transform: capitalize;
  cursor: pointer;
  transition: all 0.3s ease;

  &:hover {
    background-color: #000;
  }
`;

export const Overlay = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  z-index: 999;
`;

export const ModalContainer = styled.div`
  background: #fff;
  width: 100%;
  max-width: 500px;
  top: 50%;
  transform: translateY(-50%);
  margin: auto;
  border-radius: 8px;
  padding: 2rem;
  position: relative;
`;

export const ModalTitle = styled.h3`
  margin-bottom: 1rem;
  color: #335795;
  font-weight: bold;
  font-size: 16px;
`;

export const CloseButton = styled.button`
  position: absolute;
  top: -30px;
  right: -30px;
  border: none;
  font-size: 20px;
  font-weight: bold;
  color: #000;
  cursor: pointer;
  background: #fff;
  height: 40px;
  width: 40px;
  padding: 0 0 5px 0;
  line-height: 0;
  border-radius: 100px;
`;

export const DropArea = styled.label`
  border: 2px dashed #335795;
  border-radius: 6px;
  padding: 2rem;
  text-align: center;
  cursor: pointer;
  color: #000;
  margin-bottom: 1.5rem;
  position: relative;
  width: 100%;

  input[type="file"] {
    opacity: 0;
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    cursor: pointer;
  }
`;

export const ButtonGroup = styled.div`
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
  font-size: 14px;
`;

export const ModalBtn = styled.button`
  font-size: 14px;
  padding: 0.6rem 1.2rem;
  font-weight: 600;
  border: none;
  border-radius: 4px;
  color: #fff;
  background-color: ${({ variant }) =>
    variant === "upload" ? "#335795" : "#999"};
  cursor: pointer;
  transition: background-color 0.3s ease;

  &:hover {
    background-color: ${({ variant }) =>
      variant === "upload" ? "#a00000" : "#777"};
  }
`;


