import React, { useState } from "react";
import { Link } from "react-router-dom";
import DataTable from "react-data-table-component";
import MainHeader from "../../../components/Users/MainHeader.js";
import ModuleSideNav from "../../../components/Users/ModuleSideNav.jsx";
import "bootstrap/dist/css/bootstrap.min.css";
import {
  SectionWrapper,
  Wrapper,
} from "../../../components/Styles/MainHeadStyles.js";
import { DataRoomSection } from "../../../components/Styles/DataRoomStyle.js";

export default function InvestorReport() {
  const columns = [
    {
      // name: 'Date of Report',
      name: "Date of Report",
      selector: (row) => row.dateofreport,
      sortable: true,
    },
    {
      name: (
        <span className="tooltip-icon" tabIndex={0} aria-describedby="tooltip1">
          <div className="tooltip-text">
            <ul>
              <li>
                File naming standard: Company Name  Investor Update  Version #
                - Date
              </li>
            </ul>
          </div>{" "}
          Name of report{" "}
          <img
            className="blackdark"
            width="20"
            height="20"
            src="/assets/user/images/question.png"
            alt="Tip Image"
          />
        </span>
      ),
      selector: (row) => row.nameofreport,
      sortable: true,
    },

    {
      name: "Download",
      selector: (row) => row.download,
      sortable: true,
    },
  ];

  const data = [
    {
      id: 1,
      dateofreport: "12-12-2025",
      nameofreport: "Report 1",
      download: "Download 1",
    },
    {
      id: 2,
      dateofreport: "12-12-2030",
      nameofreport: "Report 2",
      download: "Download 2",
    },
  ];

  const customStyles = {
    rows: {
      style: {
        minHeight: "60px",
        overflow: " visible",
      },
    },
    headCells: {
      style: {
        fontSize: "14px", 
        fontWeight: "bold",
        overflow: "visible",
        "& div": {
          overflow: "visible"
        }
      },
    },
  };

  const [searchText, setSearchText] = useState("");

  // Filter data by nameofreport (case insensitive)
  const filteredData = data.filter(
    (item) =>
      item.nameofreport?.toLowerCase().includes(searchText.toLowerCase()) ||
      item.dateofreport?.toLowerCase().includes(searchText.toLowerCase()) ||
      item.download?.toLowerCase().includes(searchText.toLowerCase())
  );

  return (
    <>
      <Wrapper>
        <div className="fullpage d-block">
          <MainHeader />
          <SectionWrapper className="d-block py-5">
            <div className="container-lg">
              <div className="row">
                <div className="col-md-3">
                  <ModuleSideNav />
                </div>
                <div className="col-md-9">
                  <DataRoomSection className="d-flex flex-column gap-2">
                    <div className="titleroom d-flex justify-content-between align-items-center border-bottom pb-3">
                      <h4>Investor Update Reports</h4>
                      <Link
                        to="/add-new-investor"
                        className="btn btn-outline-dark active"
                      >
                        <span
                          className="tooltip-icon"
                          tabIndex={0}
                          aria-describedby="tooltip1"
                        >
                          <div className="tooltip-text">
                            <ul>
                              <li>
                                Investor updates should be provided quarterly.
                              </li>
                            </ul>
                          </div>{" "}
                          Add New Investor{" "}
                          <img
                            className="blackdark"
                            width="20"
                            height="20"
                            src="/assets/user/images/question.png"
                            alt="Tip Image"
                          />
                        </span>
                      </Link>
                    </div>
                    <div className="d-flex justify-content-end p-0">
                      <input
                        type="search"
                        placeholder="Search Here..."
                        className="form-control"
                        value={searchText}
                        onChange={(e) => setSearchText(e.target.value)}
                        style={{
                          padding: "10px",
                          width: "100%",
                          maxWidth: "200px",
                          fontSize: "14px",
                        }}
                      />
                    </div>
                    <div className="d-flex flex-column justify-content-between align-items-start tb-box">
                      <DataTable
                        customStyles={customStyles}
                        columns={columns}
                        className="datatb-report"
                        data={filteredData}
                        pagination
                        highlightOnHover
                        striped
                        responsive
                      />
                    </div>
                  </DataRoomSection>
                </div>
              </div>
            </div>
          </SectionWrapper>
        </div>
      </Wrapper>
    </>
  );
}
